import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {

  displayshows:boolean = true;
  displaytrees:boolean = false;
  displaytexts:boolean = false;
  displaypannings:boolean = false;

  constructor() { }

  ngOnInit(): void {
  }

  displayshow(){
   this.displayshows = true;
   this.displaytrees = false;
   this.displaytexts =false;
   this.displaypannings = false;


  }
  displaytree(){
    this.displayshows = false;
    this.displaytrees = true;
    this.displaytexts =false;
    this.displaypannings = false;


  }

  displaytext(){
    this.displayshows = false;
    this.displaytrees = false;
    this.displaytexts =true;
    this.displaypannings = false;

  }

  displaypanning(){
    this.displayshows = false;
   this.displaytrees = false;
   this.displaytexts =false;
   this.displaypannings = true;


  }
  displaypoint(){
    this.displayshows = false;
    this.displaytrees = false;
    this.displaytexts =false;
    this.displaypannings = false;

  }

}
